let clickCount = 0;

// 
const targetClicks = 10;

let timer: number;

export function initVConsole() {
    // 
    window.addEventListener('click', (event) => {
        // 
        const windowWidth = window.innerWidth;
        const windowHeight = window.innerHeight;

        // 
        const clickThreshold = 50; // 
        if (
            event.clientX >= windowWidth - clickThreshold &&
            event.clientY <= clickThreshold
        ) {
            // 
            clickCount++;

            // 
            if (clickCount === targetClicks) {
                // 

                new VConsole();

                // 
                clickCount = 0;
            }

            clearTimeout(timer);
            timer = setTimeout(() => {
                clickCount = 0;
            }, 3000);
        }
    });
}
